/* ------------------------------------------------------
   ROOT VARIABLES – Brand Colors
------------------------------------------------------ */
:root {
  --brand: #ed9261;        /* Hauptfarbe (Lokal-Wandfarbe) */
  --brand-dark: #d87842;   /* dunklere Variante */
  --dark: #1a1a1a;         /* Text */
  --cream: #faf8f6;        /* Hintergrund */
  --white: #ffffff;
}

/* ------------------------------------------------------
   BRAND FONTS
------------------------------------------------------ */
.font-logo {
  font-family: 'Allura', cursive;
  letter-spacing: 0.04em;
  font-weight: 400;
}

.font-cinzel {
  font-family: 'Cinzel', serif;
  letter-spacing: 0.5px;
}

/* ------------------------------------------------------
   GLOBAL BASE / TYPOGRAPHY
------------------------------------------------------ */
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--cream);
  color: var(--dark);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga", "kern";
}

h1, h2, h3{
  font-weight: 600;
  letter-spacing: -0.025em;
  line-height: 1.15;
  color: var(--dark);
}

.section-title{
  margin-bottom: 1.5rem;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 600;
}

@media (min-width: 768px){
  .section-title{
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

.section-title {
  color: var(--dark);
}

.subtext{
  font-size: 1.125rem;
  line-height: 1.75rem;
  line-height: 1.625;
  color: #6f6f6f;
}

/* ------------------------------------------------------
   NAVIGATION – Underline Animation
------------------------------------------------------ */
.nav-link {
  position: relative;
  padding-bottom: 2px;
}

.nav-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 0%;
  height: 2px;
  background-color: var(--brand);
  transition: width 260ms ease;
}

.nav-link:hover::after {
  width: 100%;
}

/* ------------------------------------------------------
   HEADER EFFECTS
------------------------------------------------------ */
.header-gradient {
  background: linear-gradient(
    90deg,
    rgba(237, 146, 97, 0.05) 0%,
    rgba(255, 255, 255, 0.9) 40%,
    rgba(237, 146, 97, 0.05) 100%
  );
}

.header-line {
  box-shadow: 0 1px 0 rgba(237, 146, 97, 0.4);
}

/* ------------------------------------------------------
   BUTTONS – Brand Color
------------------------------------------------------ */
.btn-brand{
  border-radius: 0.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
  --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  background-color: var(--brand);
}

.btn-brand:hover {
  background-color: var(--brand-dark);
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.btn-outline{
  border-radius: 0.5rem;
  border-width: 1px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 500;
  color: var(--dark);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  border-color: var(--dark);
}

.btn-outline:hover {
  background-color: var(--dark);
  color: var(--cream);
}

.btn-light{
  border-radius: 0.5rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1));
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 500;
  color: var(--dark);
  --tw-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --tw-shadow-colored: 0 1px 2px 0 var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

.btn-light:hover {
  background-color: #f3f3f3;
}

/* ------------------------------------------------------
   CARDS
------------------------------------------------------ */
.card{
  border-radius: 1rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(221 221 221 / var(--tw-border-opacity, 1));
  background-color: rgb(255 255 255 / 0.7);
  padding: 1.5rem;
  --tw-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --tw-shadow-colored: 0 1px 2px 0 var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  --tw-backdrop-blur: blur(8px);
